<template>
  <div>
    <div class="hoverdiv" :id="idname" :style="{width: '150px', height: '200px'}">

    </div>
  </div>
</template>

<script>
export default {
  props: ['idname', 'listdata'],
  data() {
    return {
      echartsDatalist: []
    };
  },
  mounted() {
    this.echartsDatalist = this.listdata;
    this.drawLine();
  },
  methods: {
    drawLine() {
      // 基于准备好的dom，初始化echarts实例
      let myChart = this.$echarts.init(
        document.getElementById(`${this.idname}`)
      );
      // 绘制图表
      myChart.setOption({
        backgroundColor: 'transparent',
        color:['rgb(84,112,198)','rgb(250,200,88)','rgb(145,204,117)'],
        title: {
          // text: '学习天学',
          left: 'center'
        },
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/> {b} : {c} ({d}%)'
        },
        legend: {
          formatter: (name) => {
            let dateday = null;
            switch (name) {
              case `${this.echartsDatalist[0].name}`:
                return '  ' + name + ' ' + this.echartsDatalist[0].value + `种`;
                //  + '         ' + ((this.echartsDatalist[0].value / this.echartsDatalist.length) * 100).toFixed(2) + '%'
                break;
              case `${this.echartsDatalist[1].name}`:
                return '  ' + name + ' ' + this.echartsDatalist[1].value + '';
                //  + '         ' + ((this.echartsDatalist[1].value / this.echartsDatalist.length) * 100).toFixed(2) + '%'
                break;
              case `${this.echartsDatalist[2].name}`:
                return '  ' + name + ' ' + this.echartsDatalist[2].value + '级';
                //  + '         ' + ((this.echartsDatalist[2].value / this.echartsDatalist.length) * 100).toFixed(2) + '%'
                break;
              default:
                return '   ';
                break;
            }
          },
          textStyle: {
            color: '#fff'
          },
          orient: 'vertical', // 布局方式，默认为水平布局，可选为：'horizontal' ¦ 'vertical'
          // 水平安放位置，默认为左侧，可选为：'center' | 'left' | 'right' | {number}（x坐标，单位px）
          x: '0%',
          // 垂直安放位置，默认为全图顶端，可选为：'top' | 'bottom' | 'center' | {number}（y坐标，单位px）
          y: '70%',
          // 重写legend显示样式
          data: this.echartsDatalist,
          itemGap: 2, //数据间距
          color: (255, 255, 255)
        },
        series: [
          {
            name: 'Element',
            type: 'pie',
            // roseType:'radius',
            // radius: ['20%','75%'],
            top: -50,
            clockwise: true,
            // center: ['56%', '20%'],    图地位置
            data: this.echartsDatalist,
            label: {
              // 饼图内容 显示在 饼图上 并显示 百分比
              //饼图图形上的文本标签
              normal: {
                show: true,
                position: 'inner', //标签的位置
                textStyle: {
                  fontWeight: 30,
                  fontSize: 12, //文字的字体大小
                  color:"#000"
                },
                formatter: '{d}%',
                // rotate: 325
              }
            }
            // emphasis: {  饼图样式
            //   itemStyle: {
            //     shadowBlur: 10,
            //     shadowOffsetX: 0,
            //     shadowColor: 'rgba(0, 0, 0, 0.5)'
            //   }
            // }
          }
        ]
      });
    }
  }
};
</script>

<style scoped>
.hoverdiv:hover{
	/* background: #000; */
  box-shadow: 1px 11px 10px 0px rgb(2, 2, 2);
}
</style>