<template>
  <div>
    <div id="chart-test2" :style="{ height: height?height:'700px',width:width?width:'500px'}"></div>
  </div>
</template>

<script>
export default {
  props: ['height', 'width'],
  data() {
    return {
      testOption2: {
        // backgroundColor: '', //设置背景色透明
        xAxis: {
          max: 'dataMax'
        },
        yAxis: {
          type: 'category',
          data: [
            '苹果',
            '香蕉',
            '火龙果',
            '西瓜',
            '猕猴桃',
            '哈密瓜',
            '黄瓜',
            '西红柿',
            '水蜜桃',
            '橘子'
          ],
          inverse: true,
          animationDuration: 300,
          animationDurationUpdate: 300,
          max: 9
        },
        series: [
          {
            name:'动态数据滚动',
            realtimeSort: true,
            type: 'bar',
            data: [],
            label: {
              show: true,
              position: 'right',
              valueAnimation: true
            }
          }
        ],
        legend: {
          show: true
        },
        animationDuration: 0,
        animationDurationUpdate: 3000,
        animationEasing: 'linear',
        animationEasingUpdate: 'linear'
      }
    };
  },
  mounted() {
    let chartTest2 = this.$echarts.init(
      document.getElementById('chart-test2'),
      'dark'
    );
    //初始化数据
    let test2_Data = [];
    for (let i = 0; i < 10; ++i) {
      test2_Data.push(Math.round(Math.random() * 200));
    }
    this.testOption2.series[0].data = test2_Data; //初始化数据
    chartTest2.setOption(this.testOption2); //初始化图表

    let that = this;
    setTimeout(function () {
      that.getData();
    }, 0);
    let timeclear = setInterval(function () {
      that.getData();
    }, 3000);
    setTimeout(() => {
      clearInterval(timeclear);
      //水电站虚拟仿真实例
    }, 1000);
  },
  methods: {
    getData() {
      let chartTest2 = this.$echarts.init(
        document.getElementById('chart-test2'),
        'dark'
      );
      var data = this.testOption2.series[0].data;
      for (var i = 0; i < data.length; ++i) {
        if (Math.random() > 0.9) {
          data[i] += Math.round(Math.random() * 2000);
        } else {
          data[i] += Math.round(Math.random() * 200);
        }
      }
      this.testOption2.series[0].data = data;
      chartTest2.setOption(this.testOption2); //得到数据后重新渲染图表
    }
  }
};
</script>

