<template>
  <div class="data-content">
    <header class="heads">
      <h1 style="display: flex; align-items: center; color:#fff; font-size: 20px; cursor: pointer;"> <i class="el-icon-caret-left" style="color:#fff; font-size: 40px;"></i>
        <router-link style="color:#fff" to="/elementUiApi">返回Element-Ui插件扩充</router-link>
      </h1>
      <div style="color: #fff;">
        前几个普遍比较简单，最后一个是自己通过一些项目需求自己封装的方法
        <el-button size="mini" type="primary" @click="pudelog=true" style="margin-right: 10px;">展示代码</el-button>
      </div>
      <!-- -----------------------------普通弹窗代码 ------------------------------------------------------- -->
      <el-dialog title="普通弹窗代码" :visible.sync="pudelog" width="70%">
        <pre name="" id="" cols="180" rows="100">
            &lt;el-date-picker 
              style=&quot;width: 300px;&quot; 
              :disabled=&#39;datedisabled&#39; 
              align=&quot;right&quot; 
              format=&quot;yyyy 年&quot; 
              :default-time=&quot;value7&quot; 
              :picker-options=&quot;pickerOptions2&quot; 
              value-format=&quot;yyyy&quot; 
              v-model=&quot;value7&quot; 
              type=&quot;year&quot; 
              placeholder=&quot;选择日期时间&quot;&gt;
            &lt;/el-date-picker&gt;
            这里是data数据
             datedisabled: false,
              pickerOptions2: {
                disabledDate: this.disabledDate
              }
            这里是逻辑处理
            methods: {
                disabledDate(time) {
                  let datedisabels;
                  // time是自动返回左右40年的时间数据
                  datedisabels = parseInt(&#39;2021&#39;);
                  console.log(datedisabels);
                  return (
                    (time.getFullYear() &lt;= datedisabels &amp;&amp; time.getFullYear()) ||
                    time.getFullYear() &gt;= datedisabels + 5
                  );
                }
              }
        </pre>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
        </span>
      </el-dialog>
    </header>

    <div>
      <ul class="uls">
        <li>
          <h1 style="text-align: center; color:#fff; font-size: 20px;">默认日期</h1>
          <div class="contentdata">
            <el-date-picker style="cursor: pointer;" v-model="value1" type="date" placeholder="选择日期">
            </el-date-picker>
          </div>
        </li>
        <li>
          <h1 style="text-align: center; color:#fff; font-size: 20px;">携带参数的日期</h1>
          <div class="contentdata">
            <el-date-picker v-model="value2" align="right" type="date" placeholder="选择日期" :picker-options="pickerOptions">
            </el-date-picker>
          </div>
        </li>
        <li>
          <h1 style="text-align: center; color:#fff; font-size: 20px;">年月周/多个日期</h1>
          <div class="contentdata" style="display: flex;  flex-direction: column; ">
            <el-date-picker v-model="value5" type="year" placeholder="选择年">
            </el-date-picker>
            <el-date-picker v-model="value3" type="week" format="yyyy 第 WW 周" placeholder="选择周">
            </el-date-picker>
            <el-date-picker v-model="value4" type="month" placeholder="选择月">
            </el-date-picker>
            <el-date-picker v-model="value6" placeholder="选择一个或多个日期">
            </el-date-picker>
          </div>
        </li>
        <li>
          <h1 style="text-align: center; color:#fff; font-size: 20px;">高级用法动态限制起始年和四年以后的年</h1>
          <div class="contentdata" style="display: flex;  flex-direction: column; ">
            <el-date-picker style="width: 300px;" :disabled='datedisabled' align="right" format="yyyy 年" :default-time="value7" :picker-options="pickerOptions2" value-format="yyyy" v-model="value7" type="year" placeholder="选择日期时间">
            </el-date-picker>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 弹窗
      pudelog: false,
      // 携带参数的
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now();
        },
        shortcuts: [
          {
            text: '今天',
            onClick(picker) {
              picker.$emit('pick', new Date());
            }
          },
          {
            text: '昨天',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit('pick', date);
            }
          },
          {
            text: '一周前',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', date);
            }
          }
        ]
      },
      value2: new Date(),
      //默认
      value1: new Date(),
      //
      value3: new Date(),
      value4: new Date(),
      value5: new Date(),
      value6: new Date(),
      // 限制高级写法
      value7: new Date(),
      datedisabled: false,
      pickerOptions2: {
        disabledDate: this.disabledDate
      }
    };
  },
  methods: {
    disabledDate(time) {
      let datedisabels;
      // time是自动返回左右40年的时间数据
      datedisabels = parseInt('2021');
      console.log(datedisabels);
      return (
        (time.getFullYear() <= datedisabels && time.getFullYear()) ||
        time.getFullYear() >= datedisabels + 5
      );
    }
  }
};
</script>

